<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckVote :vote="vote"/>
            <PublicityContent v-model="form" @submit="handleSubmit"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../表决列表/components/TopButtons'
    import CheckVote from '@/components/vote/check'
    import PublicityContent from '@/components/checkSuffixForm/publicityContent'
    import {getPublicizedVote, updateVotePublicityContent} from '@/api/vote'

    export default {
        components: {
            TopButtons, CheckVote, PublicityContent
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                vote: {},
                form: {publicity_content: null},
            }
        },
        methods: {
            fetch() {
                getPublicizedVote(this.thisId)
                    .then(res => {
                        this.vote = res.data;
                        this.form.publicity_content = res.data.publicity_content;
                    })
            },
            handleSubmit() {
                updateVotePublicityContent(this.thisId,this.form)
                    .then(res=>{
                        this.$message.success('操作成功');
                    })
            }
        },
        created() {
            this.fetch();
        }
    }
</script>
