<template>
    <div class="columns bac_white min_h600">
        <div class="xwsh_nr">
            <div class="pad-30">
                <h1>{{vote.title}}</h1>
                <h2>{{vote.started_at_format}}</h2>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <div v-html="publicity_content"></div>
                    </a-col>
                    <a-col :span="22">
                        <downloadFiles v-if="files.length" :list="files"/>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center">
                    <chart :data="chartData"/>
                </a-row>
            </div>
        </div>

        <slot name="suffix"></slot>
    </div>
</template>

<script>
    import downloadFiles from '@/components/download/Files'
    import Chart from './Chart'

    export default {
        components: {downloadFiles, Chart},
        props: {
            vote: {type: Object}
        },
        computed: {
            files() {
                if (!this.vote.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.vote.more.hasOwnProperty('files')) {
                    return [];
                }
                return Array.isArray(this.vote.more.files) ? this.vote.more.files : [];
            },
            chartData() {
                if (!this.vote.hasOwnProperty('result_format')) {
                    return {};
                }
                return this.vote.result_format;
            },
            initiate_content() {
                if (!this.vote.hasOwnProperty('initiate_content')) {
                    return null;
                }
                return this.vote.initiate_content;
            },
            publicity_content() {
                if (!this.vote.hasOwnProperty('publicity_content')) {
                    return null;
                }
                return this.vote.publicity_content;
            },
        },
        methods: {},
        mounted() {
        }
    }
</script>
