<template>
    <div id="main" style="height: 400px;width: 600px"></div>
</template>

<script>
    let echarts = require('echarts');

    export default {
        name: "Chart",
        props: {
            data: {required: true, type: Object,}
        },
        computed: {
            xAxisData() {
                let result = [];
                for (let key in this.data) {
                    result.push(key);
                }
                return result;
            },
            seriesData() {
                let result = [];
                for (let key in this.data) {
                    result.push(this.data[key]);
                }
                return result;
            },
        },
        methods: {
            init() {
                var myChart = echarts.init(document.getElementById('main'));
                let option = {
                    title: {
                        text: '具体票选结果',
                        x: 'center'
                    },
                    color: ['#d7aa60'],
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: [
                        {
                            type: 'category',
                            data: this.xAxisData,
                            axisTick: {
                                alignWithLabel: true
                            }
                        }
                    ],
                    yAxis: [
                        {
                            type: 'value'
                        }
                    ],
                    series: [
                        {
                            name: '票选结果',
                            type: 'bar',
                            barWidth: '40%',
                            data: this.seriesData
                        }
                    ]
                };
                myChart.setOption(option);
            },
        },
        mounted() {
            this.init();
        },
        watch: {
            data: {
                deep: true,
                handler() {
                    this.init();
                }
            }
        }
    }
</script>

